import './App.css';
import '../node_modules/bootstrap/dist/css/bootstrap.css';
import { BrowserRouter, Switch, Route } from 'react-router-dom';

// Componentes
import Personajes from './componentes/Personajes';
import NavBar from './componentes/NavBar';
import Historia from './componentes/Historia';
import DigimonList from './componentes/DigimonList';
import Digimon from './componentes/Digimon';

function App() {
  return (
    <BrowserRouter>
      <Switch>
        <main className="App">
          <div className="contenedor m-auto col-12 col-sm-8 col-md-6 col-xl-4 col-xxl-3">
              <NavBar/>
            <div className="contenido">
              <Route path="/" exact component={Historia} />
              <Route path="/personajes" exact component={Personajes} />
              <Route path="/digimon" exact component={DigimonList} />
              <Route path="/digimon/name/:name" exact component={Digimon} />
            </div>
          </div>
        </main>
      </Switch>
    </BrowserRouter>
  );
}

export default App;
