import React, { useState, useEffect } from 'react';

const Digimon = (props)=> {
    
    const [ digimon, setDigimon ] = useState([]);

    useEffect(()=> {
        addDigimon();
    }, []);

    const addDigimon = async ()=> {
        const datos = await fetch(`https://digimon-api.vercel.app/api/digimon/name/${props.match.params.name}`);
        const digimon = await datos.json();
        setDigimon(digimon);
    }

    return(
        <div className="m-auto text-center">
            {
                digimon.map(e=> 
                    <div key={e.name}>
                        <h3 className="bg-success text-white mt-4 mb-4 p-2">{e.name}</h3>
                        <img src={e.img} alt={e.name} />
                        <p className="bg-light p-1 mt-3">Nivel actual: <span className="bg-success p-1 px-3 text-white"> {e.level} </span></p>
                    </div>)
            }
        </div>
    );
}

export default Digimon;