import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

const DigimonList = ()=> {
    
    const [ listaDigimon, setListaDigimon ] = useState([]);

    useEffect(()=> {
        llamarLista();
    }, []);

    const llamarLista = async ()=> {
        try{
            const datos = await fetch('https://digimon-api.vercel.app/api/digimon');
            const addDigimon = await datos.json();
            setListaDigimon(addDigimon);
        } catch(err){
            console.log(err);
        }
    }

    return(
        <div className="">
            <h2 className="mt-4 mb-4 bg-success text-light pt-2 pb-3">¡Escoje a tu Digimon preferido!</h2>
            <div className="container">
                <div className="container">
                    <div className="container">
                        <ul className="list-group">
                            {
                                listaDigimon.map(e=>
                                    <Link key={e.name} to={`/digimon/name/${e.name}`}> 
                                    <li className="list-group-item list-group-item-success
                                    list-group-item-action p-2 mt-2"> 
                                    {e.name} </li> </Link>
                                )
                            }
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    );

}
export default DigimonList;